import React from 'react'
import { useRef } from 'react'
import { useEffect } from 'react'
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword  } from "firebase/auth";
import { useState } from 'react';


function Auth(props) {

    const [authErrorMessage, setAuthErrorMessage] = useState(null)

    const first = useRef(true)
    const input1 = useRef()
    const input2 = useRef()
    const focusOn = useRef(1)

    useEffect(()=>{
        if(first.current){
            setUpTabListener()
            input1.current.focus()
            first.current = false            
        }    
    },[])
    
    function setUpTabListener(){
        
        var authWindow = document.querySelector(".authWindow")           

        authWindow.addEventListener("keydown", event => { 
                        
            if(event.key === "Tab"){                            
                if(focusOn.current == 1){
                    input2.current.focus()
                    focusOn.current = 2                    
                }else{
                    input1.current.focus()
                    focusOn.current = 1
                }
            }

            if(event.key === "Enter"){
                login()
            }

        })
    }

    function login(){        
        signInWithEmailAndPassword(props.firebase.current.auth, input1.current.value, input2.current.value)
            .then((userCredential) => {
                // Signed in 
                const user = userCredential.user;
                // ...
            })
            .catch((error) => {
                const errorCode = error.code;
                const errorMessage = error.message;
                setAuthErrorMessage(error.message)
            });
    }
    function createUser(){        
        createUserWithEmailAndPassword(props.firebase.current.auth, input1.current.value, input2.current.value)
            .then((userCredential) => {
                // Signed in 
                const user = userCredential.user;
                //console.log("user sidned in with user id "+userCredential.user.uid)
                // ...
            })
            .catch((error) => {
                const errorCode = error.code;
                const errorMessage = error.message;
                setAuthErrorMessage(error.message)
                // ..
            });
    }

  return (
    <div className='authWindow'>
        Auth
        <div className='authWindowInner'>
            <div>
                <input ref={input1} placeholder='email' tabIndex="0"></input>
            </div>
            <div>
                <input ref={input2} type="password" placeholder='password' tabIndex="1"></input>
            </div>            
            <button onClick={createUser}>Create</button>
            <button onClick={login} >Login</button>
            <div className='errorMessage'>{authErrorMessage}</div>
        </div>
    </div>
  )
}

export default Auth